import { Injectable } from '@angular/core';

import * as io from 'socket.io-client';

import { Observable } from 'rxjs';
import { Cookie } from 'ng2-cookies/ng2-cookies';

import { HttpClient, HttpHeaders } from '@angular/common/http';
import { HttpErrorResponse, HttpParams } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class SocketService {

  private url = 'https://chatapi.edwisor.com';

  private socket;

  constructor(public http: HttpClient) {

    this.socket = io(this.url); // connection is being established

  }

  //event to be listened
  public verifyUser = () => {
    return Observable.create((Observer) => {
      this.socket.on('verifyUser', (data) => {
        Observer.next(data);
      }); //end socket
    }); //end Onservable
  } // end varifyUser

  public onlineUserList = () => {

    return Observable.create((Observer) => {
      this.socket.on('online-user-list', (userList) => {
        Observer.next(userList);
      }); //end socket
    }); //end Onservable

  }

  public disconnectedSocket = () => {
    return Observable.create((Observer) => {
      this.socket.on('disconnect', () => {
        Observer.next();
      }); //end socket
    }); //end Onservable
  }

  // event to be emitted

  public setUser = (authToken) => {

    this.socket.emit('set-user', authToken);
  }

  public markChatAsSeen = (userDetails) => {

    this.socket.emit('mark-chat-as-seen',userDetails);

  } // end markchatasseen function

  
   // end events to be emitted

   // chat related methods

   public getChat(senderId,receiverId,skip): Observable<any> {

    return this.http.get(`${this.url}/api/v1/chat/get/for/user?senderId=${senderId}&receiverId=${receiverId}&skip=${skip}&authToken=${Cookie.get('authtoken')}`);
    
  }

  public chatByUserId = (userId) => {

    return Observable.create((observer)=>{

      this.socket.on(userId, (data) => {

        observer.next(data);

      });

    });

  } // end chatByUser function

  public SendChatMessage = (chatMsgObject) =>{

    this.socket.emit('chat-msg', chatMsgObject);

  } // end sendChatMessage function

  public exitSocket = () =>{

    this.socket.disconnect();

  } // end exitSocket function

  private handleError(err: HttpErrorResponse) {
    let errorMessage = '';

    if (err.error instanceof Error) {
      errorMessage = `an error occured : ${err.error.message}`;
    }
    else {
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return Observable.throw(errorMessage);
  }


}
