import { Component, OnInit } from '@angular/core';
import { AppService } from '../../app.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';



@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  public firstName: any;
  public lastName: any;
  public mobile: any;
  public email: any;
  public password: any;
  public apiKey: any;


  constructor(public appService: AppService, public router: Router, public toastr: ToastrService) { }

  ngOnInit() {
  }

  public goToSignIn: any = () => {

    this.router.navigate(['/']);

  }// end goToSignIn

  public signupFunction: any = () => {

    if(!this.firstName)
    {
      this.toastr.warning('First Name','Missing');
    }
    else if(!this.lastName)
    {
      this.toastr.warning('Last Name','Missing');
    }
    else if(!this.mobile)
    {
      this.toastr.warning('Mobile Number','Missing');
    }
    else if(!this.email)
    {
      this.toastr.warning('Email','Missing');
    }
    else if(!this.password)
    {
      this.toastr.warning('Password','Missing');
    }
    else if(!this.apiKey)
    {
      this.toastr.warning('API Key','Missing');
    }
    else
    {
        let data = {
          firstName: this.firstName,
          lastName: this.lastName,
          mobile: this.mobile,
          email: this.email,
          password: this.password,
          apiKey: this.apiKey
        }

        this.appService.signupFunction(data).subscribe((apiResponse) => {
          console.log(apiResponse);

          if (apiResponse.status === 200) 
          {
            this.toastr.success(apiResponse.message,'Success',{timeOut: 2000});

            setTimeout(() => 
            {
              this.goToSignIn();
            }, 2000);
          }
          else 
          { 
              this.toastr.error(apiResponse.message,'Error',{timeOut: 2000});
          }
        }, (err) => {
          this.toastr.error('some error occured')
        });
      }
  }
}
